body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
    text-decoration: none !important;
    color: inherit !important;
}
.nextPage{
    display: flex;
    justify-content: space-evenly ;
    align-items: center;
    position: relative;
    top: 100vh;
}

.box{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    float: left;
}
nav{
    background-color: rgb(26, 20, 20);
    color: white;
}
.card {
    width: 340px;
    border: none;
    height: 390px;
    border-radius: 3px;
    cursor: pointer;
    position: relative;
    overflow: hidden
}

.p-info {
    display: flex;
    justify-content: space-between
}

.p-price {
    position: absolute;
    right: 5px;
    top: 13px
}

.heart i {
    font-size: 50px;
    opacity: 0;
    color: red
}

.heart i:hover {
    color: red
}

.p-image img {
    width: 230px;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: all 0.5s
}

.card:hover .heart i {
    -webkit-animation: heart 300ms ease-in forwards;
            animation: heart 300ms ease-in forwards;
    -webkit-animation-delay: 500ms;
            animation-delay: 500ms
}

.p-about {
    position: absolute;
    width: 170px;
    font-size: 10px;
    top: 140px;
    left: -125px;
    opacity: 0
}

.card:hover .p-about {
    -webkit-animation: content 300ms ease-in forwards;
            animation: content 300ms ease-in forwards
}

@-webkit-keyframes content {
    0% {
        opacity: 0
    }

    100% {
        opacity: 1;
        left: 25px
    }
}

@keyframes content {
    0% {
        opacity: 0
    }

    100% {
        opacity: 1;
        left: 25px
    }
}

@-webkit-keyframes heart {
    0% {
        opacity: 0
    }

    100% {
        opacity: 1
    }
}

@keyframes heart {
    0% {
        opacity: 0
    }

    100% {
        opacity: 1
    }
}

.card:hover .p-image img {
    -webkit-animation: onimage 300ms ease-in forwards;
            animation: onimage 300ms ease-in forwards
}

.card:hover .p-price {
    -webkit-animation: onprice 500ms ease-in forwards;
            animation: onprice 500ms ease-in forwards
}

@-webkit-keyframes onprice {
    0% {
        right: 10px;
        top: 20px
    }

    50% {
        right: 60px;
        top: 30px
    }

    100% {
        right: 267px;
        top: 70px
    }
}

@keyframes onprice {
    0% {
        right: 10px;
        top: 20px
    }

    50% {
        right: 60px;
        top: 30px
    }

    100% {
        right: 267px;
        top: 70px
    }
}

@-webkit-keyframes onimage {
    0% {
        width: 250px
    }

    50% {
        width: 200px;
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        -webkit-transform: translateX(100px);
                transform: translateX(100px)
    }

    100% {
        width: 180px;
        top: 100px;
        -webkit-transform: translateY(100px);
                transform: translateY(100px);
        -webkit-transform: translateX(110px);
                transform: translateX(110px)
    }
}

@keyframes onimage {
    0% {
        width: 250px
    }

    50% {
        width: 200px;
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        -webkit-transform: translateX(100px);
                transform: translateX(100px)
    }

    100% {
        width: 180px;
        top: 100px;
        -webkit-transform: translateY(100px);
                transform: translateY(100px);
        -webkit-transform: translateX(110px);
                transform: translateX(110px)
    }
}

.buttons {
    position: absolute;
    bottom: -150px
}

.card:hover .buttons {
    -webkit-animation: buttons 500ms ease-in forwards;
            animation: buttons 500ms ease-in forwards
}

@-webkit-keyframes buttons {
    0% {
        bottom: -100px
    }

    100% {
        bottom: 20px
    }
}

@keyframes buttons {
    0% {
        bottom: -100px
    }

    100% {
        bottom: 20px
    }
}

.btn {
    height: 50px;
    font-size: 16px;
    width: 140px !important;
    font-weight: 600
}
